:root {
	--button-color-before: linear-gradient(135deg, #b263ff59 0%, #ec545461 100%);
	--button-color-after: linear-gradient(199deg, #8a30df59 0%, #dd3c3c61 100%);
}
html,
body {
	min-height: 100%;
	width: auto;
	background-color: #141313;
	background-image: url("/public/background.svg");
	background-repeat: no-repeat;
	background-position: center;
	background-attachment: fixed;
	backdrop-filter: blur(5px);
	background-blend-mode: soft-light;
	background-size: cover;
	/* background-image: linear-gradient(0deg, #0d0c0c 0%, #18161a 100%); */
	color: whitesmoke;
}
a {
	text-decoration: none;
	color: whitesmoke;
}
.container {
	width: 100%;
	margin-left: auto;
	margin-right: auto;
}

@media (min-width: 300px) {
	.container {
		max-width: 300px;
	}

	.card-container {
		grid-template-columns: 320px;
	}
	.navbar {
		flex-wrap: nowrap !important;
	}
}
@media (min-width: 480px) {
	.container {
		max-width: 480px;
	}
	.card-container {
		margin-right: auto;
		margin-left: auto;
		grid-template-columns: 320px;
	}
	.navbar {
		flex-wrap: nowrap !important;
	}
}
@media (min-width: 640px) {
	.container {
		max-width: 640px;
	}
	.card-container {
		grid-template-columns: 250px 250px;
	}
}

@media (min-width: 768px) {
	.container {
		max-width: 768px;
	}
	.card-container {
		grid-template-columns: 210px 210px 210px;
	}
}

@media (min-width: 1024px) {
	.container {
		max-width: 1024px;
	}
	.card-container {
		grid-template-columns: 300px 300px 300px;
	}
	.edit-image-container {
		grid-template-columns: 1fr 300px;
	}
}

@media (min-width: 1280px) {
	.container {
		max-width: 1280px;
	}
	.card-container {
		grid-template-columns: 400px 400px 400px;
	}
	.edit-image-container {
		grid-template-columns: 1fr 300px;
	}
}

@media (min-width: 1536px) {
	.container {
		max-width: 1536px;
	}
	.card-container {
		grid-template-columns: 370px 370px 370px 370px;
	}
	.edit-image-container {
		grid-template-columns: 1fr 300px;
	}
}
.navbar {
	margin-top: 8px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-end;
	justify-items: center;
	align-items: center;
}

.nav-link {
	margin: 20px;
}
.web-title {
	justify-self: center;
	text-shadow: 1px 1px 1px rgb(24, 41, 201);
	color: rgb(163, 147, 147);
	text-decoration: none;
	margin-right: auto;
	font-size: x-large;
	margin-left: 10px;
}

.head-title {
	text-align: center;
	margin: 3rem;
}
.head-sub {
	text-align: center;
	color: rgba(100, 148, 237, 0.74);
	margin: 3rem;
	text-transform: capitalize;
}

.card-container {
	display: grid;
	justify-content: center;
	gap: 15px;
}

.card {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	text-align: center;
	border-radius: 8px;
	transition: all 250ms ease-in;
	background: linear-gradient(135deg, #511e8259 0%, #7b434361 100%);
}

.card:hover {
	scale: 105%;
	box-shadow: 0 0 9px rgb(35 173 255);
	background: linear-gradient(199deg, #8a49c859 0%, #a4555561 100%);
}

.footer {
	display: flex;
	flex-direction: row;
	border-top: solid;
	margin-top: 2rem;
	justify-content: center;
	align-items: center;
}

.footer-web {
	text-align: center;
	color: rgb(109, 109, 211);
	transition: all 100ms;
}

.footer-web:hover {
	color: rgb(82, 82, 228);
	text-indent: 10px;
}
.year {
	color: rgb(226, 221, 221);
}
.footer-logo {
	color: white;
}
.footer-web:hover .footer-logo {
	color: rgb(82, 82, 228);
}

.timeago {
	font-size: smaller;
	color: gray;
}

.card-category {
	flex-shrink: 1;
}

.card-title {
	color: white;
}
.card-quote {
	font-size: medium;
}
.card-button {
	cursor: pointer;
	margin-left: 5px;
	background: var(--button-color-before);
	border: 0px;
	padding: 5px 13px 5px 13px;
	border-radius: 5px;
}
.card-button:hover {
	background: var(--button-color-after);
}

.search-bar {
	color: white;
	background-color: rgba(0, 0, 0, 0.466);
	padding: 8px;
	border-radius: 10px;
	display: block;
	margin-top: 5px;
	text-align: center;
}
.search-loading {
	margin-top: 10px;
}

.search-container {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
	align-content: center;
}

.search-label {
	text-align: center;
	font-weight: bold;
}

.add-btn {
	cursor: pointer;
	border: 0px;
	color: #c9c8c8;
	padding: 9px;
	background: var(--button-color-before);
	border-radius: 10px;
	text-align: center;
	font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
	transition: all 250ms;
}

.add-btn:hover {
	background: var(--button-color-after);
}

/* Modal CSS */
.modal,
.overlay {
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	position: fixed;
}
body.active-modal {
	overflow-y: hidden;
}

.overlay {
	background: rgba(5, 4, 4, 0.4);
	backdrop-filter: blur(3px);
}
.modal-content {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	line-height: 1.4;
	background: #1d1c1c;
	padding: 14px 28px;
	border-radius: 3px;
	max-width: 600px;
	min-width: 300px;
}
.modal-form {
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: center;
}

.close-modal {
	float: right;
	color: white;
	font-weight: bold;
	font-size: medium;
	background-color: #444343;
	border-radius: 5px;
	border-color: transparent;
	transition: all 250ms;
}
.close-modal:hover {
	box-shadow: 1px 1px 3px 2px rgb(109, 87, 87);
	scale: 105%;
}

.form-input {
	cursor: pointer;
	display: block;
	margin: auto;
	margin-top: 5px;
	margin-bottom: 5px;
	padding: 8px;
	background: var(--button-color-before);
	border-radius: 5px;
	transition: all 50ms;
	border: 0px;
}
textarea.form-input {
	width: 380px;
	height: 60px;
}

.form-input:focus {
	border: 2px solid rgb(107, 107, 233);
	background: var(--button-color-before);
	color: white;
}

.form-input-lab {
	font-size: small;
	color: #c9c8c8;
	text-shadow: 1px 1px 1px gray;
}
.form-input-helper {
	color: rgb(150, 148, 148);
	margin-top: 0px;
	font-size: small;
	text-shadow: none;
}
.form-input-helper > span {
	color: rgb(143, 133, 133);
	font-size: x-small;
	font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
		"Lucida Sans", Arial, sans-serif;
	padding-left: 2px;
}

.form-input-submit {
	border: 0px;
	margin: auto;
	margin-top: 10px;
	padding: 10px 18px 10px 18px;
	border-radius: 9px;
	background: var(--button-color-before);
}

.form-input-submit:hover {
	background: var(--button-color-after);
}

.break-line {
	padding-left: 300px;
	border-width: 3px;
	border-color: #444343;
}

.modal-form-title {
	color: rgb(60, 120, 233);
}

.filter {
	margin-bottom: 2rem;
	text-align: center;
}

.clear-cat-btn {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin: auto;
	padding: 3px;
	border-radius: 8px;
	border-color: transparent;
	background: var(--button-color-before);
}

.clear-cat-btn:hover {
	background: var(--button-color-after);
}

.pagination {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 2rem;
	flex-wrap: wrap;
}

.pagination-btn {
	padding: 5px 10px 5px 10px;
	border-radius: 5px;
	background: var(--button-color-before);
}
.pagination-btn:hover {
	background: var(--button-color-after);
}
.pagination-btn-right {
	border-top-left-radius: 1px;
	border-bottom-left-radius: 1px;
}
.pagination-btn-left {
	border-top-right-radius: 1px;
	border-bottom-right-radius: 1px;
}

.page-link {
	background: var(--button-color-before);
	border-radius: 2px;
}
.page-link:hover {
	background: var(--button-color-after);
}

.active-page-btn {
	background: linear-gradient(199deg, #ac60f3b6 0%, #f55e5ec2 100%);
}

button.page-link {
	border-width: 0px;
	border-radius: 2px;
	background: var(--button-color-before);
}

.loading {
	display: flex;
	justify-content: center;
}

.pagination-loading {
	margin-top: 40px;
	margin-bottom: -20px;
}

.category-loading {
	margin-bottom: 10px;
}

.alertcard-container {
	display: grid;
	margin-left: 40%;
	margin-right: 40%;
	gap: 1px;
	margin-top: 10px;
	height: auto;
}
.alertcard-content {
	display: flex;
	flex-direction: column;
	background-color: transparent;
	border-radius: 5px;
	text-align: center;
}
.alertcard-header {
	margin-top: 0px;
	padding-top: 2px;
	margin-bottom: 0px;
	padding-bottom: 5px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
}
.alertcard-alert {
	margin-top: 0px;
	padding-bottom: 14px;
	margin-bottom: 0px;
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
	padding-top: 8px;
}

.alert-enter {
	opacity: 0;
	transform: scale(0.9);
}
.alert-enter-active {
	opacity: 1;
	transform: translateX(0);
	transition: all 2s;
}
.alert-exit {
	opacity: 1;
}
.alert-exit-active {
	opacity: 0;
	transform: scale(0.9);
	transition: all 2s;
}

/* editing text on images  */
.quoteimage-holder {
	display: flex;
	justify-content: center;
	align-items: center;
}
.edit-image {
	border: 20px solid rgba(0, 0, 0, 0.25);
}

.edit-image-container {
	margin-top: 100px;
	margin-bottom: 75px;
	box-shadow: 4px 0px 26px 6px rgb(95, 66, 104);
	-webkit-box-shadow: 4px 0px 26px 6px rgb(95, 66, 104);
	background: var(--button-color-after);
	display: grid;
	justify-content: center;
}
.edit-tools-container {
	display: flex;
	flex-direction: column;
}
.DisplayImg {
	background-color: rgb(36, 36, 36);
	position: relative;
	width: 100%;
	margin: auto;
}
.imgText {
	position: relative;
	width: 100%;
	margin: auto;
}

.DisplayImg > blockquote {
	text-rendering: optimizeLegibility;
	position: absolute;
	top: 45%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	text-align: center;
	margin: 15px 0;
	padding: 0 5px;
	text-shadow: 0px 0px black;
}
.edit-image > svg {
	z-index: 1;
	position: absolute;
	margin-left: 30%;
	margin-right: 70%;
	margin-top: 15%;
}
.DisplayImg > i {
	position: absolute;
	left: 0%;
	font-size: 0.7em;
	color: white;
	background: #000000;
	padding: 5px;
	margin: 5px;
	border-radius: 2px;
}
.edit-download-btn {
	cursor: pointer;
	color: black;
	font-size: larger;
	padding: 20px;
	background: var(--button-color-before);
	border: 0px;
	border-radius: 5px;
	margin-top: auto;
}
.edit-download-btn:hover {
	color: whitesmoke;
	background: var(--button-color-after);
}
.edit-exit-btn {
	cursor: pointer;
	color: black;
	font-size: larger;
	padding: 10px;
	margin-top: 5px;
	border-radius: 5px;
	border: 0px;
	background: var(--button-color-before);
}
.edit-exit-btn:hover {
	color: whitesmoke;
	background: var(--button-color-after);
}
.edit-tools-header {
	text-align: center;
	color: whitesmoke;
	text-transform: uppercase;
}
.hex-code-input {
	border: 0px;
	margin-top: 1px;
	margin: 8px;
	height: 40px;
	width: 92%;
	font-size: medium;
	text-align: center;
	background-color: #dddddd3b;
	border-radius: 20px;
}
.hex-code-input:focus {
	background-color: #dbc4c457;
	border: 0px;
	outline: none;
}

.text-color-label {
	margin: 10px;
	font-size: small;
	margin-bottom: 0px;
}
.changecolor-btn {
	border: 0px;
	border-radius: 5px;
	background: var(--button-color-before);
	margin-left: 33%;
	padding: 8px;
}
.changecolor-btn:hover {
	background: var(--button-color-after);
}
.image {
	display: block;
}
.color-name-input {
	background: #dddddd3b;
	padding-top: 20px;
	padding-bottom: 20px;
	color: black;
}
.font-name-input {
	margin-top: 1px;
}
.editquote-img {
	margin-top: 5px;
	margin-left: 10px;
	display: none;
}
.img-upload {
	margin-top: 1px;
}
.img-upload > label {
	border: 0px solid;
	display: inline-block;
	padding: 3px 8px;
	cursor: pointer;
	background: var(--button-color-before);
	margin-left: 40%;
	border-radius: 5px;
}
.img-upload > p {
	text-transform: uppercase;
	margin-top: -15px;
	text-align: center;
}
.img-upload > label:hover {
	background: var(--button-color-after);
}

.select-design {
	border-radius: 5px;
	border: 0px;
	padding: 8px;
	margin-left: 10px;
	background: var(--button-color-before);
}
.select-design:hover {
	background: var(--button-color-after);
}
.edit-properties {
	display: flex;
	flex-direction: row;
}

/* reload img css */
.reload-img {
	margin-left: 12px;
}
.reload-img > img {
	cursor: pointer;
	width: 8%;
	margin: -6px;
}
.reloadit {
	-webkit-animation: rotate-center 2s ease-in-out infinite both;
	animation: rotate-center 2s ease-in-out infinite both;
}

@-webkit-keyframes rotate-center {
	0% {
		-webkit-transform: rotate(0);
		transform: rotate(0);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes rotate-center {
	0% {
		-webkit-transform: rotate(0);
		transform: rotate(0);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.edit-tool-header {
	text-align: center;
	margin-top: 70px;
	margin-bottom: -20px;
	color: rgba(100, 148, 237, 0.74);
}
.edit-tool-header > h3 {
	text-transform: uppercase;
}

/* quote move css */
.edit-move-quote {
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.movequote-label {
	margin-top: 18px;
	margin-right: 8px;
	font-size: small;
}
.movebtn {
	padding: 3px 8px 5px 8px;
	border: 0px;
	background: var(--button-color-before);
}
.movebtn:hover {
	background: var(--button-color-after);
}
.movequote .moveup {
	padding-left: 20px;
	padding-right: 20px;
	border-top-left-radius: 70px;
	border-top-right-radius: 70px;
	margin-left: 2px;
}
.movequote .movedown {
	border-bottom-left-radius: 70px;
	border-bottom-right-radius: 70px;
	display: block;
	margin-left: auto;
}
